import React from "react";
import cx from "classnames";

let LinkButton = ({ icon, size, url }) => (
  <a
    href={url}
    className={cx("btn-" + size, "btn btn-default")}
    target="_blank"
    rel="noopener noreferrer"
  >
    <i className={cx("fa", icon ? "fa-" + icon : "")} />
  </a>
);

LinkButton.defaultProps = {
  size: "sm"
};

export { LinkButton };



